// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import App from './App';
//引入第三方组件
// import VueCropper from 'vue-cropperjs';
//引入路由
import VueRouter from 'vue-router';
import VueResource from 'vue-resource';
import axios from 'axios';
// import VueAxios from 'vue-axios'
//引入自定义组件
import Home from './components/Pages/Home/Home';
//引入饿了吗组件（需要注意的是，样式文件需要单独引入element-ui/lib/theme-chalk/index.css）
import Element from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import './assets/icon/iconfont.css'
import global_ from './components/Global'//引用文件
import VueParticles from 'vue-particles'  // 粒子特效
Vue.config.productionTip = false
//注册（告诉Vue使用路由组件）
Vue.use(VueRouter);
Vue.use(VueResource);
Vue.use(VueParticles);
Vue.prototype.$axios = axios;
Vue.prototype.GLOBAL = global_//挂载到Vue实例上面
//注册 饿了吗组件
Vue.use(Element, { size: 'small', zIndex: 3000 });
Vue.http.options.headers = {
    'Content-Type': 'application/json;charset=utf-8'
}
//配置路由
const routes = [
    {
        path: '/',
        component: Home,
    }

]
const router = new VueRouter({
    routes,
    // mode: 'history'
});

//注册全局组件
// Vue.component("users", Users)
/* eslint-disable no-new */
new Vue({
    //实例化中使用配置路由对象
    router,
    el: '#app',
    //根组件App.vue作为局部组件，嵌套到main.js中
    components: { App },
    //因此可以直接使用<App/>
    template: '<App/>'
})


// 全局路由守卫
router.beforeEach((to, from, next) => {
    console.log('导航守卫');
    // to: Route: 即将要进入的目标 路由对象
    // from: Route: 当前导航正要离开的路由
    // next: Function: 一定要调用该方法来 resolve 释放这个钩子。执行效果依赖 next 方法的调用参数。
    //我在这里模仿了一个获取用户信息的方法
    let isLogin = window.sessionStorage.getItem('user');
    if (isLogin) {
        //如果用户信息存在则往下执行。
        console.log("即将进入的页面" + to.path);
        console.log("即将离开的页面" + from.path);
        next()
    } else {
        //如果用户token不存在则跳转到根路径，根路径默认是Login页面页面
        if (to.path === '/') {
            console.log("即将进入的页面" + to.path);
            console.log("即将离开的页面" + from.path);
            next()
        } else {
            console.log("即将进入的页面" + to.path);
            console.log("即将离开的页面" + from.path);
            next('/')
        }
    }
})

export default router;






