      // 疫情统计数据
      statistics: [],
      // 新闻列表
      tableData: [],
      // 肺炎同程查询列表
      tableData_ncovsame: [],
      // 谣言鉴别查询列表
      tableData_rumour: [],
      // 各城市疫情信息报告查询列表
      tableData_ncovcity: [],
      // 肺炎同程查询抽屉
      drawer_ncovsame: false,
      // 谣言鉴别查询抽屉
      drawer_rumour: false,
      // 各城市疫情信息报告抽屉
      drawer_ncovcity: false,
      // 抽屉展开方向
      direction: "rtl",
      // loading效果
      loading: false,
      // 各城市疫情信息报告弹窗控制
      dialogVisible: false,
      // 谣言鉴别
      rumour_form: {
        word: "", // 关键字
        num: 10, // 返回数量
        page: 0 // 是否翻页，默认1
      },
      // 肺炎同程查询
      ncovsame_form: {
        type: "飞机", // 交通类型：1飞机，2火车，3地铁，4长途客车/大巴，5公交车，6出租车，7轮船，8其他公共场所（默认飞机）
        date: "", // 时间
        no: "", // 车次
        arrive: "", //到达地点
        num: 10, // 返回数量，默认10
        page: 0 // 是否翻页，默认1
      },
      // 谣言鉴别查询表单规则
      rules: {
        word: [
          { required: true, message: '请输入谣言关键字"', trigger: "blur" },
          {
            min: 2,
            max: 100,
            message: "长度在 2 到 100 个字符",
            trigger: "blur"
          }
        ]
      },
      // 各城市疫情信息报告
      tbodyData: [] // 用于传递给子组件HealthRecures.vue
    };
  },
  methods: {
    // 列表字段格式化
    formatter(row, column) {
      return this.transferTime(row.createTime);
    },
    // 时间转换，将后台返回的时间为一串数字转成正常格式
    transferTime(cTime) {
      var jsonDate = new Date(parseInt(cTime));
      Date.prototype.format = function(format) {
        var o = {
          "y+": this.getFullYear(),
          "M+": this.getMonth() + 1,
          "d+": this.getDate(),
          "h+": this.getHours(),
          "m+": this.getMinutes(),
          "s+": this.getSeconds()
        };

        if (/(y+)/.test(format)) {
          format = format.replace(
            RegExp.$1,
            (this.getFullYear() + "").substr(4 - RegExp.$1.length)
          );
        }

        for (var k in o) {
          if (new RegExp("(" + k + ")").test(format)) {
            format = format.replace(
              RegExp.$1,
              RegExp.$1.length == 1
                ? o[k]
                : ("" + o[k]).substr("" + o[k].length)
            );
          }
        }
        return format;
      };
      var newDate = jsonDate.format("yyyy-MM-dd hh:mm:ss");
      return newDate;
    },
    // 抽屉关闭
    handleClose(done) {
      this.$confirm("确认关闭？")
        .then(_ => {
          done();
        })
        .catch(_ => {});
    },
    // 抽屉表单取消按钮
    drawer_close() {
      // 肺炎同程查询抽屉
      this.drawer_ncovsame = false;
      // 谣言鉴别查询抽屉
      this.drawer_rumour = false;
      // 各城市疫情信息报告抽屉
      this.drawer_ncovcity = false;
    },
    // 打开抽屉窗口按钮
    rumour() {
      // 谣言鉴别查询抽屉
      this.drawer_rumour = true;
    },
    // 打开抽屉窗口
    ncovsame() {
      // 肺炎同程查询抽屉
      this.drawer_ncovsame = true;
    },
    // 打开抽屉窗口
    ncovcity() {
      // 各城市疫情信息报告抽屉
      this.drawer_ncovcity = true;
      // 各城市疫情信息报告弹窗
      this.dialogVisible = true;
    },

    // 谣言鉴别查询按钮
    rumour_onSubmit(formName) {
      // 显示加载效果
      this.loading = true;
      // //console.log("谣言鉴别查询按钮 submit!");
      this.$refs[formName].validate(valid => {
        if (valid) {
          // TODO 请求后端 谣言鉴别API
          // 请求后端
          axios
            .get(
              `${global_.rumour_url}?key=${global_.key}&word=${this.rumour_form.word}&num=${this.rumour_form.num}&page=${this.rumour_form.page}`
            )
            .then(async response => {
              //响应200 去掉loading遮罩
              if (response.status === 200) {
                this.loading = false;
              }
              // 获取新闻信息
              this.tableData_rumour = response.data["newslist"];
              //console.log(JSON.stringify(this.tableData_rumour));
            })
            .catch(async error => {
              console.error(error);
            });
        } else {
          // 不显示加载效果
          this.loading = false;
          //console.log("error submit!!");
          return false;
        }
      });
    },
    // 重置表单按钮
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    // 肺炎同程查询
    ncovsame_onSubmit(formName) {
      // 显示加载效果
      this.loading = true;
      // //console.log("肺炎同程查询按钮 submit!");
      // 下拉列表选项转成接口
      var type = 1;
      switch (this.ncovsame_form.type) {
        case "飞机":
          type = 1;
          break;
        case "火车":
          type = 2;
          break;
        case "地铁":
          type = 3;
          break;
        case "长途客车/大巴":
          type = 4;
          break;
        case "公交车":
          type = 5;
          break;
        case "出租车":
          type = 6;
          break;
        case "轮船":
          type = 7;
          break;
        case "其他公共场所":
          type = 8;
          break;
        default:
          break;
      }

      this.$refs[formName].validate(valid => {
        if (valid) {
          // TODO 请求后端 谣言鉴别API
          // 请求后端
          axios
            .get(
              `${global_.ncovsame_url}?key=${global_.key}&type=${type}&date=${this.ncovsame_form.date}&no=${this.ncovsame_form.no}&arrive=${this.ncovsame_form.arrive}&num=${this.ncovsame_form.num}&page=${this.ncovsame_form.page}`
            )
            .then(async response => {
              //响应200 去掉loading遮罩
              if (response.status === 200) {
                this.loading = false;
              }
              // 获取接口响应body
              this.tableData_ncovsame = response.data["newslist"];
              //console.log(JSON.stringify(this.tableData_ncovsame));
            })
            .catch(async error => {
              console.error(error);
            });
        } else {
          // 不显示加载效果
          this.loading = false;
          //console.log("error submit!!");
          return false;
        }
      });
    }
  },
  watch: {},
  computed: {},
  components: {
    echarts,
    HealthRecurse
  },
  created() {
    // 请求后端
    axios
      .get(global_.server_url + "/get-new-data")
      .then(async response => {
        // 获取新闻信息
        this.tableData = response.data;
      })
      .catch(async error => {
        console.error(error);
      });
  },
  beforeCreate() {},
  mounted() {
    // 请求后端
    axios
      .post(global_.server_url + "/get-data", {
        data_source: 2,
        type: 2
      })
      .then(async response => {
        // 调试输出
        await console.log(response.data["msg"]["case"]); // case各省疫情数据
        // 字符串处理
        var case_data = response.data["msg"]["case"];
        // 遍历取出想要的关键字段和值，放入新数组中
        for (let index = 0; index < case_data.length; index++) {
          const element = case_data[index];
          // 将新数组赋值给定义好的data变量statistics
          this.statistics.push({
            name: case_data[index]["provinceShortName"],
            value: case_data[index]["confirmedCount"],
            curedCount: case_data[index]["curedCount"],
            deadCount: case_data[index]["deadCount"]
          });
          // 将第三方平台（天行数据API）各地疫情数据返回的json格式，转成适用于子组件HealthRecurse.vue的格式tbodyData
          // 首先构建第一层数组
          this.tbodyData.push({
            name: case_data[index]["provinceShortName"],
            confirmNum: case_data[index]["confirmedCount"],
            cured: case_data[index]["curedCount"],
            death: case_data[index]["deadCount"],
            children: []
          });
          // 将各省份省内地区疫情数据放入children
          // 在第一层数组基础上，放入第二层数组children
          for (let index2 = 0;index2 < case_data[index]["cities"].length;index2++
          ) {
            this.tbodyData[index]["children"].push({
              name: case_data[index]["cities"][index2]["cityName"],
              confirmNum: case_data[index]["cities"][index2]["confirmedCount"],
              cured: case_data[index]["cities"][index2]["curedCount"],
              death: case_data[index]["cities"][index2]["deadCount"]
            });
          }
        }
        // 调试输出
        //console.log(JSON.stringify(this.tbodyData));
        // echart
        // 初始化echarts实例
        this.chinachart = echarts.init(document.getElementById("china_map"));
        // 进行相关配置
        this.chartOption = {
          tooltip: {
            // 鼠标移到图里面的浮动提示框
            // formatter详细配置： https://echarts.baidu.com/option.html#tooltip.formatter
            formatter(params, ticket, callback) {
              // params.data 就是series配置项中的data数据遍历
              let name, value, curedCount, deadCount;

              if (params.data) {
                name = params.data.name;
                value = params.data.value;
                curedCount = params.data.curedCount;
                deadCount = params.data.deadCount;
              } else {
                // 为了防止没有定义数据的时候报错写的
                name = 0;
                value = 0;
                curedCount = 0;
                deadCount = 0;
              }
              let htmlStr = `
          <div style='font-size:18px;'> ${params.name}</div>
          <br>
          <p style='text-align:left;margin-top:-10px;'>
	          确诊：${value}<br/>
	          治愈：${curedCount}<br/>
	          死亡：${deadCount}<br/>
          </p>
        `;
              return htmlStr;
            }
            // backgroundColor:"#ff7f50",//提示标签背景颜色
            // textStyle:{color:"#fff"} //提示标签字体颜色
          },
          // visualMap的详细配置解析：https://echarts.baidu.com/option.html#visualMap
          visualMap: {
            // 左下角的颜色区域
            type: "piecewise", // 定义为分段型 visualMap
            min: 0,
            max: 1000,
            itemWidth: 40,
            bottom: 60,
            left: 20,
            pieces: [
              // 自定义『分段式视觉映射组件（visualMapPiecewise）』的每一段的范围，以及每一段的文字，以及每一段的特别的样式
              { gt: 0, lte: 9, label: "0 ~ 9", color: "	#FFFFE0" }, // (0, 9]
              { gt: 10, lte: 99, label: "10 ~ 99", color: "#FFF68F" }, // (10, 99]
              { gt: 100, lte: 999, label: "100 ~ 999", color: "#FFC1C1" }, // (100, 999]
              { gt: 1000, lte: 9999, label: "1000 ~ 9999", color: "#FFA54F" }, // (1000, 9999]
              {
                gt: 10000,
                lte: 99999,
                label: "10000 ~ 99999",
                color: "#FF3030"
              }, // (10000, 10000]
              { value: 0, label: "0", color: "#FFFFFF" } // [0]
            ]
          },
          // geo配置详解： https://echarts.baidu.com/option.html#geo
          geo: {
            // 地理坐标系组件用于地图的绘制
            map: "china", // 表示中国地图
            // roam: true, // 是否开启鼠标缩放和平移漫游
            zoom: 1.2, // 当前视角的缩放比例（地图的放大比例）
            label: {
              show: true
            },
            itemStyle: {
              // 地图区域的多边形 图形样式。
              borderColor: "rgba(0, 0, 0, 0.2)",
              emphasis: {
                // 高亮状态下的多边形和标签样式
                shadowBlur: 20,
                shadowColor: "rgba(255, 0, 0, 0.5)"
              }
            }
          },
          series: [
            {
              name: "", // 浮动框的标题（上面的formatter自定义了提示框数据，所以这里可不写）
              type: "map",
              geoIndex: 0,
              label: {
                show: true,
                normal: {
                  textStyle: {
                    fontSize: 15,
                    fontWeight: "bold",
                    color: "red"
                  }
                }
              },
              // 这是需要配置地图上的某个地区的数据，根据后台的返回的数据进行拼接（下面是我定义的假数据）
              data: this.statistics
            }
          ]
        };
        // 使用刚指定的配置项和数据显示地图数据
        this.chinachart.setOption(this.chartOption);
      })
      .catch(async error => {
        await console.error(error);
      });
  },
  updated() {},
  destroyed() {}
};
</script>

