<template>
  <div class="health-recurse">
    <div
      class="health-recurse-wrap"
      v-for="(item, index) in healthData"
      :key="index"
    >
      <div class="health-recurse-item">
        <div class="name" @click="nameClickHandler(item)">
          {{ item.children ? (item.isExpanded ? "\u25BC" : "\u25BA") : null
          }}{{ item.name }}
        </div>
        <div class="confirmNum">
          {{
            item.confirmNum !== undefined
              ? item.confirmNum
              : getTotal(item.children, "confirmNum")
          }}
        </div>
        <div class="death">
          {{
            item.death !== undefined
              ? item.death
              : getTotal(item.children, "death")
          }}
        </div>
        <div class="cured">
          {{
            item.cured !== undefined
              ? item.cured
              : getTotal(item.children, "cured")
          }}
        </div>
      </div>
      <HealthRecurse
        v-if="item.children && item.isExpanded"
        :healthData="item.children"
      />
    </div>
  </div>
</template>

<script>
export default {
  // 组件名
  name: "HealthRecurse",
  // 用于接收父组件传递过来的值，通常是一个字符串或数组对象。（在父组件复用子组件的位置:healthData="要传递过来的数据对象变量"）
  props: ["healthData"],
  data() {
    return {};
  },
  methods: {
    getTotal: function(data, key) {
      if (data[key]) return data[key];
      else return data.map(i => i[key]).reduce((a, b) => a + b);
    },
    nameClickHandler: function(option) {
      const { isExpanded } = option;
      this.$set(option, "isExpanded", !isExpanded);
    }
  },
  mounted() {}
};
</script>

<style lang="less" scoped>
.health-recurse {
  .health-recurse-item {
    display: flex;
    margin-top: 2px;
    background: #dcdcdc;
    .name {
      cursor: pointer;
      width: 25%;
      height: 95%;
      line-height: 40px;
    }
    .confirmNum {
      cursor: pointer;
      height: 95%;
      width: 25%;
      line-height: 40px;
      // background-color: skyblue;
    }
    .death {
      // background-color: red;
      height: 95%;
      width: 25%;
      line-height: 40px;
    }
    .cured {
      // background-color: green;
      height: 95%;
      width: 25%;
      line-height: 40px;
    }
  }
}
</style>


